import React, { useState } from "react";
import { TransitionGroup, CSSTransition } from "react-transition-group";

import "./index.scss";
import Fade from "react-reveal/Fade";
import ArrowLeft from "assets/icon/arrow-left.svg";
import ArrowRight from "assets/icon/arrow-right.svg";
import Button from "elements/Button";

export default function ImageCarousel(props) {
  // Set 'width' variable from props
  const widths = props.width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 530;
  // const breakpointNull = 0;
  const breakpointXs3 = 280.5;
  const breakpointXs2 = 359.5;
  const breakpointXs = 469.5;
  const breakpointSm = 767.5;
  // const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  // Set current image state
  const [curImage, setImage] = useState(0);

  // Initialize array variable
  const classname = [];

  // Responsive Logic
  widths > breakpointXs && widths < breakpointSm && classname.push("sm");
  widths > breakpointXs2 && widths < breakpointXs && classname.push("xs");
  widths > breakpointXs3 && widths < breakpointXs2 && classname.push("xs2");
  widths < breakpointXs3 && classname.push("xs3");

  // Handle Onclick image and set className=['active']
  const handle = (pos) => {
    return curImage === pos ? " active" : " ";
  };

  return (
    <>
      <Fade bottom>
        <div className="container-carousel" style={{ position: "relative" }}>
          <div className="box">
            <Button
              type="link"
              href={process.env.REACT_APP_PATH + "/image-show"}
            >
              <svg
                width="30"
                height="22"
                viewBox="0 0 30 30"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <rect
                  x="0.5"
                  y="0.5"
                  width="29"
                  height="29"
                  stroke="white"
                  strokeDasharray="11 1"
                />
                {/* <rect
                x="0.5"
                y="0.5"
                width="29"
                height="29"
                stroke="white"
                stroke-dasharray="3 3"
              /> */}
                {/* <path d="M0.5 0.5L7.5 7.5M29.5 29.5L22.5 22.5" stroke="white" />
                <path
                  d="M0.5 29.5L7.5 22.5M29.5 0.499999L22.5 7.5"
                  stroke="white"
                /> */}
                <rect
                  x="7.25"
                  y="7.25"
                  width="15.5"
                  height="15.5"
                  rx="0.75"
                  stroke="white"
                  strokeWidth="1"
                />
                <path
                  d="M11.3364 16.2658L7.5 22.5H22.5L18.1919 14.8411C18.1119 14.6989 17.9044 14.7072 17.836 14.8554L15 21L13 18.25L11.6731 16.2597C11.5922 16.1384 11.4128 16.1416 11.3364 16.2658Z"
                  fill="white"
                  stroke="white"
                  strokeWidth="0.5"
                />
                <circle
                  cx="14"
                  cy="13"
                  r="1.75"
                  fill="white"
                  stroke="white"
                  strokeWidth="0.5"
                />
              </svg>
            </Button>
          </div>
          <div
            className="plus-img"
            // When current image [less than < data image -1] set [current image + 1]
            onClick={() =>
              curImage !== props.data.imageUrls.length - 1
                ? setImage(curImage + 1)
                : setImage(0)
            }
          >
            <img
              src={ArrowRight}
              alt=""
              style={{
                padding: 7,
                width: 30,
                height: 30,
              }}
            />
          </div>
          <div
            className="minus-img"
            // When current image [more than > 0] set [current image - 1]
            onClick={() =>
              curImage !== 0
                ? setImage(curImage - 1)
                : setImage(props.data.imageUrls.length - 1)
            }
          >
            <img
              src={ArrowLeft}
              alt=""
              style={{
                width: 30,
                height: 30,
                padding: 7,
              }}
            />
          </div>
          {/* image */}
          <TransitionGroup>
            <CSSTransition timeout={800} classNames="imgs" key={curImage}>
              <div>
                <div className="thumbnail">
                  <img
                    className={classname.join("")}
                    src={
                      process.env.REACT_APP_PATH +
                      props.data.imageUrls[curImage].url
                    }
                    alt=""
                  />
                </div>
              </div>
            </CSSTransition>
          </TransitionGroup>
        </div>
      </Fade>

      <div style={{ marginTop: 20, marginBottom: 20 }}>
        <div className="row justify-content-center img-focus">
          {/* Map Image Clicked  */}
          {props.data.imageUrls.map((item, index) => {
            return (
              <Fade
                duration={1000}
                delay={1000}
                key={`image-carousel-click${index}`}
              >
                <img
                  //
                  className={handle(index)}
                  onClick={() => {
                    setImage(index);
                  }}
                  src={process.env.REACT_APP_PATH + item.url}
                  alt=""
                  height="50px"
                  width="50px"
                  style={{
                    margin: "10px 10px 10px 10px",
                    objectFit: "cover",
                    borderRadius: 3,
                  }}
                  tabIndex="0"
                />
              </Fade>
            );
          })}
        </div>
      </div>
    </>
  );
}
