import React from "react";
import "./index.scss";
import { CSSTransition } from "react-transition-group";

export default function Scroll({ scrollY, path }) {
  // const [show, setShow] = useState(false);

  function ScrollTop() {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }

  const butn = [];
  butn.push(
    <div key={`arrow`} onClick={ScrollTop}>
      <svg
        className="arrow-up-btn"
        width="32"
        height="18"
        viewBox="0 0 32 18"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path d="M30 16L16 3L2 16" stroke="white" strokeWidth="4" />
      </svg>
    </div>
  );

  return (
    <>
   
        <CSSTransition
          in={scrollY > 1400 && scrollY < 2450}
          timeout={300}
          classNames="btns"
          unmountOnExit
          
        >
          <>{butn}</>
        </CSSTransition>
 
    </>
  );
}
