import React, { useState } from "react";

export default function SearchSamples() {
  const peoples = [
    "Siri",
    "Alexa",
    "Google",
    "Facebook",
    "Twitter",
    "Linkedin",
    "Sinkedin",
  ];

  const [searchTerm, setSearchTerm] = useState("");
  const handleChange = (event) => {
    setSearchTerm(event.target.value);
  };

  
  const results = !searchTerm
    ? peoples
    : peoples.filter((person) =>
        person.toLowerCase().includes(searchTerm.toLocaleLowerCase())
      );


 

  return (
    <div>   
        <input
          onChange={handleChange}
          value={searchTerm}
          placeholder="Typing heres ..."
          type="text"
          style={{
            width: "100%",
            height: 45,
            border: "none",
            borderRadius: 5,

            padding: 10,
            paddingLeft: 60,
            backgroundColor: "#ecf0f1",
          }}
        />
      <ul>
        {results.map((item) => (
          <li>{item}</li>
        ))}
      </ul>
    </div>
  );
}
