import React from "react";
import BrandIcon from "parts/IconText";
import IconSearch from "assets/icon/search.svg";
import IconBag from "assets/icon/bag.svg";


export default function isCart(props) {
  return (
    <div
      style={{
        position: "fixed",
        backgroundColor: "white",
        boxShadow: "0 2px 8px 0 rgba(0,0,0,0.08)",
        top: 0,
        right: 0,
        left: 0,
        padding: 20,
        justifyContent: "center",
        display: "flex",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          width: 1200,
          padding: "0px 20px",
        }}
      >
        <BrandIcon isCart></BrandIcon>
        <div style={{ display: "flex", alignItems: "center" }}>
          <img
            src={IconSearch}
            alt=""
            style={{ marginRight: 10, transform: "translateY(4px)" }}
          />
          <div style={{ position: "relative" }}>
            <img src={IconBag} alt="" style={{ marginRight: 10 }} />
            <div
              style={{
                backgroundColor: "crimson",
                color: "white",
                borderRadius: 10,
                display: "flex",
                position: "absolute",
                height: 15,
                width: 15,
                right: 2,
                top: 13,
              }}
            >
              <span style={{ margin: "auto", fontSize: 10 }}>
                {props.countCartItems}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
