import React, { useState } from "react";
import Fade from "react-reveal/Fade";
import { InputNumber } from "elements/Form";
import Button from "elements/Button";

import Chat from "assets/icon/chat.svg";
import NumLength from "utils/FormatLengthNumber";
import ReadMore from "utils/ReadMore";

export default function PageDetailDescription({ data, width }) {
  // Set state Input Number
  const [value, setVal] = useState(1);

  // Set 'width' variable from props
  const widths = width;

  // Set 'screen size' variable from props
  const breakpointCustom = 290.5;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  // const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  // const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  return (
    <>
      <Fade delay={500} duration={1800}>
        <div className="mx-2">
          {/* Readmore Description and Parser HTML String to React Element */}
          <ReadMore>{data.desk}</ReadMore>
          <span
            style={{ fontSize: 30, fontWeight: "bold", color: "#1ABC9C" }}
          >{`$ ${data.price}`}</span>
          <span style={{ fontSize: 30, fontWeight: 300 }}> per cake</span>
        </div>
        <div className="mt-3 mx-2">
          <span style={{ display: "inline-block", marginBottom: 20 }}>
            How Much your order
          </span>
          <InputNumber
            max={30}
            // still thinking :v
            onChange={(e) => setVal(e.target.value)}
            name="value"
            value={value}
          />
          <span style={{ display: "inline-block", marginBottom: 20 }}>
            {/* Render Price value and cut string [max length = 5]  */}
            You will pay ${NumLength(data.price * value)} USD per {value} pcs
          </span>
          <div className="row">
            {/* When widths [less than < 290px] set className=['col-12 mb-2'] not ['col-6'] */}
            <div
              className={widths < breakpointCustom ? "col-12 mb-2" : "col-6"}
            >
              <Button
                className="btn py-2 d-flex justify-content-center"
                type="link"
                href="#"
                isPrimary
                hasShadow
                style={{ borderRadius: 25, border: "2px solid #474795" }}
                onClick={()=>{alert('implement checkout')}}
              >
                
                Order Now
              </Button>
            </div>
            {/* When widths [less than < 290px] set className=['col-12 mb-2'] not ['col-6'] */}
            <div className={widths < breakpointCustom ? "col-12" : "col-6"}>
              <Button
                className="btn py-2 d-flex justify-content-center"
                type="link"
                href=""
                hasShadow
                style={{ borderRadius: 25, border: "2px solid #474795" }}
              >
                <img src={Chat} alt="" style={{ marginRight: 10 }} />
                <span style={{ color: "#474795" }}>Message</span>
              </Button>
            </div>
          </div>
        </div>
      </Fade>
    </>
  );
}
