import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { increment, decrement } from "pages/Example/redux/actions/counter";

export default function ExampleRedux() {
  const counter = useSelector((state) => state.counter);
  const dispatch = useDispatch();

  return (
    <div>
      <button onClick={() => dispatch(increment())}>tambah 1</button>
      <button onClick={() => dispatch(decrement())}>kurang 1</button>
      value :{counter.value}
    </div>
  );
}
