import React, { useState } from "react";
import Header from "parts/header/Header";

import data from "./data";
import Main from "./Main";
import Column from "./Column";
import "./index.scss";
import Button from "elements/Button";
export default function Cart() {


  const { product } = data;
  const [cartItems, setCartItems] = useState([]);

  const onAdd = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);

    if (exist) {
      setCartItems(
        cartItems.map((x) =>
          x.id === product.id ? { ...exist, qty: exist.qty + 1 } : x
        )
      );
    } else {
      setCartItems([...cartItems, { ...product, qty: 1 }]);
    }
  };

  const onRemove = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist.qty === 1) {
      setCartItems(cartItems.filter((x) => x.id !== product.id));
    } else {
      setCartItems(
        cartItems.map((x) =>
          x.id === product.id ? { ...exist, qty: exist.qty - 1 } : x
        )
      );
    }
  };

  const onDelete = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist.qty !== undefined) {
      setCartItems(cartItems.filter((x) => x.id !== product.id));
    }
  };

  const itemsPrice = cartItems.reduce((a, c) => a + c.price * c.qty, 0);
  const taxPrice = itemsPrice * 0.14;
  const shippingPrice = itemsPrice > 2000 ? 50 : 0;
  const totalPrice = itemsPrice + taxPrice + shippingPrice;

  return (
    <section>
      <Header countCartItems={cartItems.length} isCart notif={cartItems} />

      <div className="container top-margin">
        <h6 style={{ textAlign: "center" }}>Sample data</h6>
        <Main product={product} onAdd={onAdd}></Main>
        <h4 className=" mb-4">Cart Product</h4>

        <div className="row">
          <div className="col-lg-7">
            <Column
              onRemove={onRemove}
              onDelete={onDelete}
              onAdd={onAdd}
              cartItems={cartItems}
            ></Column>
          </div>

          <div className="col-lg-5">
            <div
              className="card"
              style={{
                borderRadius: "5px 5px 0 0",
                marginTop: 10,
                width: "100%",
                height: "fit-content",
                overflow: "hidden",
                backgroundColor: "white",
                padding: "20px 20px 20px 20px",
                boxShadow:
                  "0 5px 10px 0 rgba(0, 0, 0, 0.05), 0 10px 10px 0 rgba(0, 0, 0, 0.05)",
              }}
            >
              <div style={{ display: "flex", flexWrap: "wrap" }}>
                <input
                  placeholder="Coupon"
                  style={{
                    marginRight: 10,
                    marginBottom: 10,
                    maxWidth: 200,
                    height: "100%",
                    padding: 10,
                    borderRadius: 5,
                    border: "1px solid #bdc3c7 ",
                  }}
                />

                <Button
                  href=""
                  type="link"
                  isPrimary
                  style={{
                    padding: 10,
                    borderRadius: 5,
                    height: "fit-content",
                  }}
                >
                  Apply
                </Button>
              </div>
            </div>
            <div
              className="card"
              style={{
                borderRadius: "0 0 5px 5px",
                marginTop: -5,
                width: "100%",
                height: "fit-content",
                overflow: "hidden",
                backgroundColor: "white",
                padding: "20px 20px 20px 20px",
                boxShadow:
                  "0 5px 10px 0 rgba(0, 0, 0, 0.05), 0 10px 10px 0 rgba(0, 0, 0, 0.05)",
              }}
            >
              <div className="row">
                <div className="col">Items Price </div>
                <div className="col">${itemsPrice.toFixed()}</div>
              </div>
              <div className="row">
                <div className="col">Tax Price </div>
                <div className="col">${taxPrice.toFixed()}</div>
              </div>
              <div className="row mb-2">
                <div className="col">Shipping Price </div>
                <div className="col">${shippingPrice.toFixed()}</div>
              </div>
              <div>
                <hr />
                <div className="row mt-2">
                  <div className="col"> Total Price </div>
                  <div className="col">${totalPrice.toFixed()}</div>
                </div>
              </div>
              <div className="row mt-4">
                <div className="col">
                  <Button
                    type="link"
                    href="#"
                    onClick={() => {
                      alert("implement checkout");
                    }}
                    isPrimary
                    isLarge
                    className="d-flex justify-content-center"
                    style={{ borderRadius: 25 }}
                  >
                    Checkout
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
