import "./index.scss";

import Searc from "assets/icon/search.svg";
import Close from "assets/icon/close.svg";
import React from "react";

export default function Search(props) {
  const width = props.width;

  // const breakpointCustom = 400;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  // const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  const stylesBack = {
    position: "absolute",
    width: 45,
    height: 45,
    right: 20,
    backgroundImage: `url(${Close})`,
    backgroundRepeat: "no-repeat",
    zIndex: 1,
    borderRadius: "5px 0px 0px 5px",
    backgroundSize: 25,
    backgroundPosition: "center",
  };

  const stylesPrimary = {
    position: "absolute",
    width: 45,
    height: "100%",
    backgroundColor: "#95a5a6",
    padding: 9,
    zIndex: 1,
    borderRadius: "5px 0px 0px 5px",
  };

  const styles = {
    position: "absolute",
    width: 45,
    height: 45,
    backgroundImage: `url(${Searc})`,
    backgroundRepeat: "no-repeat",
    zIndex: 1,
    borderRadius: "5px 0px 0px 5px",
    backgroundSize: 25,
    backgroundPosition: "center",
  };
  const stylesBackDeks = {
    position: "absolute",
    width: 27,
    height: 45,
    backgroundImage: `url(${Close})`,
    backgroundRepeat: "no-repeat",
    zIndex: 1,
    right: 0,
    borderRadius: "5px 0px 0px 5px",
    backgroundSize: 20,
    backgroundPosition: "center",
    cursor: "pointer",
  };
  const stylesDesk = {
    position: "absolute",
    width: 45,
    height: 45,
    backgroundImage: `url(${Searc})`,
    backgroundRepeat: "no-repeat",
    zIndex: 1,
    borderRadius: "5px 0px 0px 5px",
    backgroundSize: 25,
    backgroundPosition: "center",
    
  };

  if (props.isSearch) {
    return (
      <>
        <div style={styles} />
        <input
          onChange={props.handle}
          value={props.searchInp}
          placeholder="Typing heres ..."
          type="text"
          style={{
            width: "100%",
            height: 45,
            border: "none",
            borderRadius: 5,

            padding: 10,
            paddingLeft: 60,
            backgroundColor: "#ecf0f1",
          }}
        />
      </>
    );
  }
  if (props.isSearchDesktop) {
    return (
      <>
        <div style={{ position: "relative" }}>
          <div style={stylesBackDeks} onClick={props.handlerdesk} />
          <div style={stylesDesk} />
          <input
            onBlur={(e)=>{e.target.value=""}}
            onChange={props.handlersrdesk}
            placeholder="Search"
            type="text"
            style={{
              width: 120,
              height: "100%",
              border: "1.5px solid #95a5a6",
              borderRadius: 10,
              padding: 10,
              paddingLeft: 40,
              backgroundColor: "#ecf0f1",
            }}
          />
        </div>
      </>
    );
  }

  return (
    <div className={`container px-4 ${width > breakpointXs ? "d-none " : ""}`}>
      <div className="row ">
        <div className="col" style={{ marginTop: 100 }}>
          <div className="input-icon">
            <div style={stylesPrimary}>
              <svg
                width="100%"
                height="100%"
                viewBox="0 0 35 34"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <circle
                  cx="13.6251"
                  cy="13.523"
                  r="8.11705"
                  transform="rotate(-46.4384 13.6251 13.523)"
                  stroke="white"
                  strokeWidth="2"
                />
                <path
                  d="M17.7956 20.5167L20.5522 17.6181L27.7985 24.5095C28.5989 25.2707 28.6307 26.5366 27.8695 27.337V27.337C27.1083 28.1374 25.8424 28.1692 25.042 27.408L17.7956 20.5167Z"
                  fill="white"
                />
              </svg>
            </div>

            <div style={stylesBack} onClick={props.handler} />
            <input
              onChange={props.handlersr}              
              autoFocus
              placeholder="Typing here ..."
              type="text"
              style={{
                width: "100%",
                height: 45,
                border: "1.5px solid #95a5a6",
                borderRadius: 5,
                padding: 10,
                paddingLeft: 60,
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
