import React from "react";
import Searc from "assets/icon/search.svg";
import { useHistory } from "react-router-dom";

export default function Item(props) {
  const { val } = props;

  const history = useHistory();

  if (props.isTabSearch) {
    return (
      <div
        className="card"
        style={{
          borderRadius: 0,
          padding: 10,
          height: "fit-content",
          border: "none",
        }}
      >
        <div
          className="row"
          onClick={() => {
            let path = "Tablet";
            history.push(path);
          }}
        >
          <div className="col-auto">
            <img
              src={props.data.imageUrl}
              alt=""
              style={{
                width: 50,
                height: 50,
                objectFit: "cover",
                borderRadius: 5,
              }}
            />
          </div>
          <div className="col" style={{ cursor: "pointer" }}>
            <div>{props.data.title}</div>
            {props.data.isBadge ? (
              <div
                style={{
                  marginTop: 5,
                  marginBottom: 5,
                  backgroundColor: "#e84118",
                  width: "fit-content",

                  borderRadius: 5,
                  padding: "2px 10px 2px 10px",
                  color: "white",
                  fontSize: 12,
                }}
              >
                new
              </div>
            ) : (
              ""
            )}
          </div>
        </div>
      </div>
    );
  }

  if (props.isDesktop) {
    return (
      <>
        <div className="container">
          <div
            style={{
              backgroundColor: "#ecf0f1",

              width: 200,
              padding: 10,
            }}
          >
            <div
              className="row"
              onClick={() => {
                let path = "id2323534/dekstop";
                history.push(path);
              }}
              style={{ cursor: "pointer" }}
            >
              <div className="col-1">
                <div
                  style={{
                    width: 20,
                    height: 20,
                    backgroundSize: 15,
                    backgroundPosition: "center",
                    backgroundImage: `url(${Searc})`,
                    backgroundRepeat: "no-repeat",
                  }}
                ></div>
              </div>
              <div className="col-auto">
                {" "}
                <h6 className="font-weight-light m-0">{val.title}</h6>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

  return (
    <>
      <div className="container px-3">
        <div
          style={{
            backgroundColor: "#ecf0f1",
            outline: "3px solid white",
            padding: 10,
          }}
        >
          <div
            className="row"
            onClick={() => {
              let path = "Mobile";
              history.push(path);
            }}
          >
            <div className="col-1">
              <div
                style={{
                  width: 20,
                  height: 20,
                  backgroundSize: 15,
                  backgroundPosition: "center",
                  backgroundImage: `url(${Searc})`,
                  backgroundRepeat: "no-repeat",
                }}
              ></div>
            </div>
            <div className="col-auto">
              {" "}
              <h6 className="font-weight-light m-0">{val.title}</h6>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
