import React from "react";
import Lottie from "react-lottie";
import Button from "elements/Button";

export default function Animate(props) {
  if (props.is404) {
    return (
      <>
        <Lottie
          options={props.data}
          height={props.height}
          width={props.width}
        />
        <div className="text-center">
          <h1>Page Not Found</h1>
          <p>Silahkan Kembali</p>
          <Button
            type="link"
            isPrimary
            is
            isLarge
            href={process.env.REACT_APP_PATH}
            style={{ borderRadius: 25 }}
          >
            Back
          </Button>
        </div>
      </>
    );
  }

  return (
    <>
      <Lottie options={props.data} height={props.height} width={props.width} />
    </>
  );
}
