import React from "react";
import Button from "elements/Button";
import IconText from "parts/IconText";
import PhoneIcon from "assets/icon/phone.svg";
import LocationIcon from "assets/icon/location.svg";
import EmailIcon from "assets/icon/email.svg";
export default function Footer() {
  return (
    <footer>
      <div className="container">
        <div className="row">
          <div className="col-lg-3 col-sm-12 mr-5 ">
            {/* Brand Icon */}
            <IconText />

            <p className="brand-tageline mt-3">
              Cakes make you be happy in everywhere moment
            </p>
          </div>
          <div className="col-lg-2 col-sm-3 col-4  mr-5 mb-4">
            <h6 className="mt-2">Menu</h6>
            <ul className="list-group list-group-flush">
              <li className="list-group-item">
                <Button type="link" href="/cupcake">
                  Cupcake
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/sandwich">
                  Sandwich
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/cakes">
                  Cakes
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/bread">
                  Bread
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/biscuits">
                  Biscuits
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/cookies">
                  Cookies
                </Button>
              </li>
            </ul>
          </div>
          <div className="col-lg-2 col-sm-3 col-4 mr-5 mb-4">
            <h6 className="mt-2">Services</h6>
            <ul className="list-group list-group-flush">
              <li className="list-group-item">
                <Button type="link" href="/home-delivery">
                  Home Delivery
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/office-delivery">
                  Office Delivery
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/event">
                  Event
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/birthday">
                  Birthday
                </Button>
              </li>
              <li className="list-group-item">
                <Button type="link" href="/restaurant">
                  Restaurant
                </Button>
              </li>
            </ul>
          </div>
          <div className="col-lg-3 col-sm-5 ">
            <h6 className="mt-2">Contact</h6>
            <ul className="list-group list-group-flush">
              <li className="list-group-item">
                <img src={LocationIcon} alt="Location Icon" style={{marginRight:10}} />
                <span>Fluuddy St. Surabaya </span>
              </li>
              <li className="list-group-item">
                <img src={PhoneIcon} alt="Phone Icon" style={{marginRight:10}} />
                {/* isExternal Link Phone Call */}
                <Button isExternal type="link" href="tel:+62212455144">
                  021-2455-144
                </Button>
              </li>

              <li className="list-group-item">
                <img src={EmailIcon} alt="Email Icon" style={{marginRight:10}} />
                 {/* isExternal Link Write Email */}
                <Button isExternal type="link" href="mailto:fluddy@gmail.com">
                  fluddy@gmail.com
                </Button>
              </li>
            </ul>
          </div>
        </div>
        <div className="row">
          <div className="col text-center copyrights">
            Copyright 2021 - All right reserved - Fluuddy
          </div>
        </div>
      </div>
    </footer>
  );
}
