import React from "react";
import Item from "../Item";


export default function SrResultDetails(props) {
  return (
    <div className="container px-4 " style={{ marginTop: 65 }}>
      <div className="row">
        {props.search > 0 && props.newDataResult.length > 0 && (
          <div className="col-lg-6 col-sm-5">
            <h5 className="mt-4">Search</h5>
            {props.newDataResult.map((item, index) => {
              return <Item key={`search-${index}`} isTabSearch data={item} />;
            })}
          </div>
        )}
        <div className="col-lg-6 col-sm-7  mb-4">
          <h5 className="mt-3">Other Menu</h5>
          <div className="container-grid sr">
            {props.dataOther.other.map((item, index) => {
              return (
                <div
                  key={`search-2-${index}`}
                  className={`item ${index === 0 ? "column-5" : "column-3"} ${
                    index === 0 ? "row-2" : "row-1"
                  } `}
                >
                  <div
                    className="card"
                    style={{ border: "none", borderRadius: 10 }}
                  >
                    {item.isBadge && (
                      <div
                        className={`tag ${
                          index > 0 ? "tag-sm" : "tag-round-sm"
                        }`}
                      >
                        <span>{item.title}</span>
                      </div>
                    )}
                    <img
                      src={item.imageUrl}
                      alt=""
                      className="h-100"
                      style={{ objectFit: "cover", borderRadius: 10 }}
                    />
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
