import React from "react";
import Fade from "react-reveal/Fade";
import HeaderCart from "parts/header/otherprops/isCart";
import Mobile from "parts/header/mainres/Mobile";
import Tablet from "parts/header/mainres/Tablet";
import Desktop from "parts/header/mainres/Desktop";

export default function Header(props) {
  // Set 'width' variable from props
  const width = props.width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 410;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  // const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  if (props.isCart) {
    return <HeaderCart />;
  }

  return (
    <Fade duration={400}>
      <header className={width < breakpointMd ? "sm" : "lg"}>
        <div className="container px-1">
          <nav
            className={`navbar navbar-expand-lg navbar-light ${
              width < breakpointMd ? "sm" : "lg"
            } `}
          >
            <Mobile props={props} />
            <Tablet props={props} />
            <Desktop props={props} />
          </nav>
        </div>
      </header>
    </Fade>
  );
}
