import React, { Component } from "react";
import Header from "parts/header/Header";

import Search from "elements/Search";
import SrResultMobile from "elements/Search/State/mainres/SrResultMobile";
import SrResultDesktop from "elements/Search/State/mainres/SrResultDesktop";

import Hero from "parts/Hero";
import MostFavorite from "parts/MostFavorite";

import landingPage from "json/landingPage.json";
import Categories from "parts/Categories";
import Testimony from "parts/Testimony";
import Footer from "parts/Footer";

import ScrollBtn from "elements/ScrollBtn";
import data from "json/MainSearch.json";
import { CSSTransition } from "react-transition-group";

export default class LandingPage extends Component {
  constructor(props) {
    super(props);

    // Create ref for handle btn 'show me'
    this.refMostFavorite = React.createRef();

    // Binding data from 'this' head component
    this.handler = this.handler.bind(this);
    this.handlersr = this.handlersr.bind(this);
    this.handlersrdesk = this.handlersrdesk.bind(this);
    this.handlerdesk = this.handlerdesk.bind(this);
  }

  // Set state component
  state = {
    screenWidth: window.innerWidth,
    scrollYaxis: window.scrollY,
    show: false,
    searchInp: "",
    searchInpDesk: "",
  };
  handlerdesk() {
    this.setState({
      searchInpDesk: undefined,
    });
  }

  handlersrdesk(e) {
    this.setState({
      searchInpDesk: e.target.value,
    });
  }

  // Handler & setState search mobile binding
  handler() {
    this.setState({
      show: !this.state.show,
      searchInp: undefined,
    });
  }

  // Handler & setState sr filter

  handlersr(e) {
    this.setState({
      searchInp: e.target.value,
    });
  }

  // Handle Event DOM
  handleResize = () => {
    this.setState({ screenWidth: window.innerWidth });
  };
  handleScroll = () => {
    this.setState({ scrollYaxis: window.scrollY });
  };

  // Every time render in the DOM for the first time 'mount'
  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    window.addEventListener("scroll", this.handleScroll);
  }
  // Detach from DOM 'unmount'
  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
    window.removeEventListener("scroll", this.handleScroll);
  }

  // Rendering element to DOM
  render() {
    // Destructure this state
    const { screenWidth, scrollYaxis, show } = this.state;
    const { match } = this.props;
    // Type:array

    // Set 'screen size' variable
    // const breakpointCustom = 400;
    const breakpointNull = 0;
    // const breakpointXs3 = 280.5;
    // const breakpointXs2 = 359.5;
    const breakpointXs = 469.5;
    // const breakpointSm = 767.5;
    // const breakpointMd = 991.5;
    const breakpointLg = 1200.5;

    const results = !this.state.searchInp
      ? ""
      : data.search.filter((val) =>
          val.title
            .toLowerCase()
            .includes(this.state.searchInp.toLocaleLowerCase())
        );

    const resultsdesk = !this.state.searchInpDesk
      ? ""
      : data.search.filter((val) =>
          val.title
            .toLowerCase()
            .includes(this.state.searchInpDesk.toLocaleLowerCase())
        );


    return (
      <>
        {/* Main Function Component */}
        <Header
          {...this.props}
          width={screenWidth}
          scrollY={scrollYaxis}
          notif={2}
          handlerdesk={this.handlerdesk}
          handler={this.handler}
          handlersrdesk={this.handlersrdesk}
        />

        {/* Search Result Dekstop */}
        {resultsdesk && screenWidth > breakpointLg ? (
          <SrResultDesktop data={resultsdesk} />
        ) : null}

        {/* Search & Search Result Mobile */}
        {show && (
          <Search
            width={screenWidth}
            handler={this.handler}
            handlersr={this.handlersr}
          />
        )}

        {screenWidth < breakpointXs && (
          <CSSTransition
            in={results !== undefined && results.length > 0}
            timeout={1000}
            classNames="sr"
            unmountOnExit
          >
            <div className="anim-sr">
              {results &&
              show &&
              screenWidth > breakpointNull &&
              screenWidth < breakpointXs ? (
                <SrResultMobile data={results} />
              ) : null}
            </div>
          </CSSTransition>
        )}

        {/* Main Function Component */}
        <Hero
          refMostFavorite={this.refMostFavorite}
          data={landingPage.hero}
          width={screenWidth}
          show={show}
        />

        <MostFavorite
          refMostFavorite={this.refMostFavorite}
          data={landingPage.mostFavorite}
          width={screenWidth}
        />
        <Categories data={landingPage.categories} width={screenWidth} />
        <Testimony data={landingPage.testimonial} width={screenWidth} />

        {/* Button Scroll Top Y axis 'Position Fixed'  */}
        <ScrollBtn scrollY={scrollYaxis} path={match.path} />
        {/* Main Function Component */}
        <Footer />
      </>
    );
  }
}
