import React from "react";
import Button from "elements/Button";
import IconBag from "assets/icon/bag.svg";
import IconLogin from "assets/icon/login.svg";
import Searcing from "elements/Search";
import BrandIcon from "parts/IconText";

export default function Desktop({ props }) {
  // Set 'width' variable from props
  const width = props.width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 410;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  // const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  // const breakpointMd = 991.5;
  const breakpointLg = 1200.5;

  // Handle navlink
  const getNavLinkClass = (path) => {
    return props.location.pathname === path ? " active" : "";
  };

  return (
    width > breakpointLg && (
      <>
        <Button href={process.env.REACT_APP_PATH} type="link">
          <BrandIcon />
        </Button>
        <div>
          <ul className="navbar-nav lg">
            {/* Navlink Menu */}
            <li
              className={`nav-item ${getNavLinkClass(
                process.env.REACT_APP_PATH
              )}`}
            >
              <Button
                className="nav-link"
                type="link"
                href={process.env.REACT_APP_PATH}
              >
                Home
              </Button>
            </li>
            <li
              className={`nav-item ${getNavLinkClass(
                process.env.REACT_APP_PATH + "/service"
              )}`}
            >
              <Button
                className="nav-link"
                type="link"
                href={process.env.REACT_APP_PATH + "/service"}
              >
                Service
              </Button>
            </li>
            <li
              className={`nav-item ${getNavLinkClass(
                process.env.REACT_APP_PATH + "/menu"
              )}`}
            >
              <Button
                className="nav-link"
                type="link"
                href={process.env.REACT_APP_PATH + "/menu"}
              >
                Menu
              </Button>
            </li>
            <li
              className={`nav-item ${getNavLinkClass(
                process.env.REACT_APP_PATH + "/contact"
              )}`}
            >
              <Button
                className="nav-link"
                type="link"
                href={process.env.REACT_APP_PATH + "/contact"}
              >
                Contact
              </Button>
            </li>
          </ul>
        </div>
        <div>
          <ul className="navbar-nav">
            <li
              className={`search-desktop ${getNavLinkClass(
                process.env.REACT_APP_PATH + "/search"
              )}`}
            >
              <Searcing
                isSearchDesktop
                handlersrdesk={props.handlersrdesk}
                handlerdesk={props.handlerdesk}
              />
            </li>

            <li
              className={`cart ${getNavLinkClass(
                process.env.REACT_APP_PATH + "/cart"
              )}`}
            >
              <Button type="link" href={process.env.REACT_APP_PATH + "/cart"}>
                {/* Render Icon Cart */}
                <div
                  className="notif"
                  style={{
                    marginRight: 15,
                    marginLeft: 7,
                    position: "relative",
                  }}
                >
                  <img src={IconBag} alt="" />
                  <div className="buble">
                    {/* Render Bubbles Notif Cart */}
                    <span>
                      {props.notif.cart !== undefined
                        ? props.notif.cart.length
                        : "3"}
                    </span>
                  </div>
                </div>
              </Button>
            </li>
            <li
              className={`nav-item ${getNavLinkClass(
                process.env.REACT_APP_PATH + "login"
              )}`}
            >
              <Button
                className="login nav-link text-center"
                type="link"
                href={process.env.REACT_APP_PATH + "/login"}
                isPrimary
                isBlock
              >
                <img src={IconLogin} alt="" style={{ marginRight: 10 }} />
                <span
                  style={{
                    fontSize: 16,
                    color: "white",
                  }}
                >
                  Login
                </span>
              </Button>
            </li>
          </ul>
        </div>
      </>
    )
  );
}
