import React from "react";
import Fade from "react-reveal/Fade";
import Star from "elements/Star";

export default function Testimony({ data, width }) {
  // Set 'width' variable from props
  const widths = width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 400;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  // const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  return (
    <Fade>
      <section className="container">
        <div className="row ">
          <div className="col-lg-4 col-sm-6 my-auto">
            {/* When width [less than < 469px] set className=['d-flex justify-content-center'] */}
            <div
              className={`testimonial-hero ${
                widths < breakpointXs ? "d-flex justify-content-center" : ""
              }`}
              style={{ marginBottom: 50 }}
            >
              <img
                src={process.env.REACT_APP_PATH + data.imageUrl}
                alt="testimonial"
              />
            </div>
          </div>
          <div className="col-lg-5 col-sm-6 ">
            <div className="content-center-sm">
              <h4 style={{ textTransform: "uppercase" }}>{data.name}</h4>
              <h2
                className="h3 font-weight-light"
                style={{ marginBottom: 25, lineHeight: 1.5, fontSize: 20 }}
              >
                {data.desk}
              </h2>

              <div className="row">
                <div className="col-auto">
                  <img src={data.user.image} style={{ width: 56 }} alt="" />
                </div>
                <div className="col-7">
                  <h6 className="font-weight-normal">{data.user.name}</h6>
                  <h6 style={{ fontSize: 12 }} className="font-weight-light">
                    {data.user.profession}
                  </h6>
                </div>
              </div>
              <div className="row" style={{ marginTop: 25 }}>
                <div className="col-auto pr-0">
                  {/* Element Star */}
                  <Star
                    value={data.user.rate}
                    width={35}
                    height={35}
                    spacing={4}
                  />
                </div>
                <div className="col-1 my-auto " style={{ marginLeft: 8 }}>
                  <h5 className="h6">{data.user.rate}</h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Fade>
  );
}
