import "./index.scss";
import Button from "elements/Button";
import $ from "jquery";
import React, { Component } from "react";

export default class ExampleImageSlider extends Component {
  constructor() {
    super();
    this.scroll = this.scroll.bind(this);
  }

  scroll(direction) {
    let far = ($(".image-container").width() / 2) * direction;
    let pos = $(".image-container").scrollLeft() + far;
    $(".image-container").animate({ scrollLeft: pos }, 500);
  }

  render() {
    return (
      <div className='sample'>
  <div className="main">
        <h1 className="title">Scrolling Image Slider</h1>
        <div className="wrapper">
          <Button type="link" href="#" onClick={this.scroll.bind(null, -1)}>
            <svg
              width="18"
              height="31"
              viewBox="0 0 18 31"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M16.5 1.5L3.5 15.5L16.5 29.5"
                stroke="black"
                strokeWidth="2"
              />
            </svg>
          </Button>

          <div className="image-container">
            <div className="image">1</div>
            <div className="image">2</div>
            <div className="image">3</div>
            <div className="image">4</div>
            <div className="image">5</div>
            <div className="image">6</div>
            <div className="image">7</div>
            <div className="image">8</div>
            <div className="image">9</div>
            <div className="image">10</div>
          </div>
          <Button type="link" href="#" onClick={this.scroll.bind(null, 1)}>
            <svg
              width="18"
              height="31"
              viewBox="0 0 18 31"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M1.5 29.5L14.5 15.5L1.5 1.5"
                stroke="black"
                strokeWidth="2"
              />
            </svg>
          </Button>
        </div>
      </div>
      </div>
    
    );
  }
}
