import React from "react";
import "./index.scss";
import Anim from "elements/Animate";
import animationData from "assets/anim/box.json";
import { ReactComponent as ReactLogo } from "assets/icon/delete.svg";

export default function Column({ cartItems, onAdd, onRemove, onDelete }) {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  return (
    <div>
      <div className="card outercard">
        <div>
          {cartItems.length === 0 && (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                flexDirection: "column",
                textAlign: "center",
              }}
            >
              <span>Cart is empty</span>
              <span style={{ fontSize: 12 }}>
                please add product from sample data
              </span>
              <div>
                <Anim data={defaultOptions} width={200} height={200}></Anim>
              </div>
            </div>
          )}
        </div>
        {cartItems.map((item) => {
          return (
            <div className="container">
              <div className="thumbnail">
                <img src={item.image} alt="" />
              </div>

              <div className="content">
                <h6>{item.name}</h6>
                <div className="numbers">
                  <button className="removecart" onClick={() => onRemove(item)}>
                    -
                  </button>
                  <input
                    type="text"
                    placeholder="0"
                    value={item.qty}
                    disabled
                  />
                  <button className="addcart" onClick={() => onAdd(item)}>
                    +
                  </button>
                </div>

                <h6>
                  price {item.qty} x ${item.qty * item.price.toFixed()}
                </h6>
              </div>
              <div className="action">
                {/* <img src={Delete} alt=""  /> */}
                <ReactLogo
                  onClick={() => onDelete(item)}
                  style={{ cursor: "pointer" }}
                />
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
