import React, { useState } from "react";
import Button from "elements/Button";
import Menu from "assets/icon/menu.svg";
import IconSearch from "assets/icon/search.svg";
import IconBag from "assets/icon/bag.svg";
import BrandIcon from "parts/IconText";
import IconLogin from "assets/icon/login.svg";
import { CSSTransition } from "react-transition-group";

export default function Mobile({ props }) {
  const [isNavCollapsed, setIsNavCollapsed] = useState(false);

  // Set 'width' variable from props
  const width = props.width;

  // Set 'screen size' variable from props
  const breakpointCustom = 350;
  // const breakpointNull = 0;
  const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  // Handle navlink
  const getNavLinkClass = (path) => {
    return props.location.pathname === path ? " active" : "";
  };

  return (
    width < breakpointMd && (
      <>
        <Button href={process.env.REACT_APP_PATH} type="link">
          <BrandIcon />
        </Button>
        <div className="nav-right">
          <ul>
            {width > breakpointCustom ? (
              <>
                <li>
                  <Button
                    type="link"
                    href={
                      props.isDetailSr
                        ? "/search"
                        : "" || width < breakpointXs
                        ? "#"
                        : "/search"
                    }
                    onClick={props.handler}
                  >
                    {/* Render Icon Search */}
                    <img src={IconSearch} alt="" />
                  </Button>
                </li>
                <li>
                  <Button type="link" href="/cart">
                    {/* Render Icon Cart */}
                    <div className="notif" style={{ position: "relative" }}>
                      <img src={IconBag} alt="" />
                      <div className="buble">
                        {/* Render Bubbles Notif Cart */}
                        <span>
                          {/* {props.notif !== undefined ? props.notif.length : ":v"} */}
                          3
                        </span>
                      </div>
                    </div>
                  </Button>
                </li>
              </>
            ) : (
              ""
            )}
            <li>
              <Button
                className={`navbar-toggler ${
                  width < breakpointXs3 ? "xs" : ""
                }`}
                // Handle toggler
                onClick={() => setIsNavCollapsed(!isNavCollapsed)}
              >
                <img src={Menu} alt="menu" style={{ width: "100%" }} />
              </Button>
            </li>
          </ul>
        </div>

        <CSSTransition
          in={isNavCollapsed}
          timeout={1000}
          classNames="mob"
          unmountOnExit
        >
          <div className={`navbar-collapse`}>
            <ul className="navbar-nav">
              <li className={`nav-item ${getNavLinkClass("/")}`}>
                <Button className="nav-link" type="link" href="/">
                  Home
                </Button>
              </li>
              <li className={`nav-item ${getNavLinkClass("/service")}`}>
                <Button className="nav-link" type="link" href="/service">
                  Service
                </Button>
              </li>
              <li className={`nav-item ${getNavLinkClass("/menu")}`}>
                <Button className="nav-link" type="link" href="/menu">
                  Menu
                </Button>
              </li>
              <li className={`nav-item ${getNavLinkClass("/contact")}`}>
                <Button className="nav-link" type="link" href="/contact">
                  Contact
                </Button>
              </li>
              <li className={`nav-item ${getNavLinkClass("/login")}`}>
                <Button
                  className="login-mobile nav-link text-center"
                  type="link"
                  href="/login"
                  isPrimary
                  isBlock
                >
                  <img src={IconLogin} alt="" style={{ marginRight: 10 }} />
                  <span
                    style={{
                      fontSize: 16,
                      color: "white",
                    }}
                  >
                    Login
                  </span>
                </Button>
              </li>
            </ul>
          </div>
        </CSSTransition>
      </>
    )
  );
}
