import React from "react";
import { InputNumber } from "elements/Form";
import { InputDate } from "elements/Form";
import { useState } from "react";

export default function ExampleRFC() {
  const [duration, setVAl] = useState({
    vals: { duration: 1 },
  });
  const [data, setDate] = useState({
    data: {
      duration: 1,
      date: {
        startDate: new Date(),
        endDate: new Date(),
        key: "selection",
      },
    },
  });

 
  return (
    <>
      <div className="container">
        <h1>RFC</h1>
        <div className="row" style={{ width: 300 }}>
          <div className="col">
            <InputNumber
              max={30}
              onChange={(e) => setVAl({ vals: { duration: e.target.value } })}
              name="duration"
              value={duration.vals.duration}
            ></InputNumber>

            <InputDate
              max={30}
              onChange={(e) =>
                setDate({
                  data: {
                    date: e.target.value,
                  },
                })
              }
              name="value"
              value={data.data.date}
            />
          </div>
        </div>
      </div>
    </>
  );
}
