import React, { useEffect, useState } from "react";
import { CSSTransition } from "react-transition-group";

import "./index.scss";
export default function ExampleTransition2() {
  // const [showMessage, setShowMessage] = useState(false);

  const size = useWindowSize();

  // Hook
  function useWindowSize() {
    // Initialize state with undefined width/height so server and client renders match
    // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
    const [windowSize, setWindowSize] = useState({
      width: undefined,
    });
    useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
        // Set window width/height to state
        setWindowSize({
          width: window.scrollY,
        });
      }
      // Add event listener
      window.addEventListener("scroll", handleResize);
      // Call handler right away so state gets updated with initial window size
      handleResize();
      // Remove event listener on cleanup
      return () => window.removeEventListener("scroll", handleResize);
    }, []); // Empty array ensures that effect is only run on mount
    return windowSize;
  }

  return (
    <div>
      <div className="trans2">
        <h1>Title</h1>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit.
          Reprehenderit atque dignissimos deserunt doloribus incidunt eius sint
          alias dolorum adipisci voluptatum magni voluptatem, quo sit quisquam
          nemo numquam voluptates saepe veritatis maiores odit recusandae
          architecto ducimus. Eaque, aliquid tenetur, ab maiores consectetur
          assumenda nemo aut labore consequuntur, quidem officiis. Tenetur
          delectus dolore recusandae cupiditate eaque facere cum distinctio
          necessitatibus quod quaerat dignissimos dolorum veritatis sequi, ut
          odio ad repellat. Magnam atque, in veritatis eaque reiciendis sunt
          maxime laborum sapiente id quaerat. Accusamus ducimus odit eligendi
          doloremque, unde neque eius asperiores nemo inventore animi beatae
          enim dolorem ea dolore est dolor maxime?
        </p>
        <p>
          Lorem ipsum dolor sit amet consectetur, adipisicing elit. Doloribus,
          accusantium esse enim delectus natus explicabo, nobis quod eos amet
          quas necessitatibus accusamus! Quisquam nobis minus consectetur
          officia dolorum, doloribus debitis, eum at et doloremque sequi qui,
          reiciendis odit cumque delectus magni unde placeat? Ipsa voluptates
          nisi sapiente beatae non iusto. Reprehenderit in eligendi quas quidem
          inventore perspiciatis autem similique obcaecati totam iure quia
          voluptatem ex illo fugit ea aut quod perferendis, temporibus quisquam
          cumque optio architecto? Itaque autem optio nulla explicabo ratione
          voluptatum adipisci magni perspiciatis ipsa exercitationem, eaque nam
          mollitia cupiditate odio rem illum dolorum quidem sunt reprehenderit
          voluptatibus in iusto deserunt modi? Veniam delectus, pariatur
          inventore quisquam consequatur quia eius tempora perspiciatis error,
          dolorem iste! Excepturi quae aperiam dolorem odio, libero
          reprehenderit eum officia corrupti iure amet tempore earum nesciunt
          facere exercitationem, voluptatum minima voluptate, repellat pariatur
          quasi aspernatur eius placeat. Libero labore non aliquam
          exercitationem, vel pariatur eaque, doloremque doloribus totam ex
          iusto veniam quam sapiente eveniet dolor quasi voluptate cupiditate et
          laudantium a. Minima sint, accusantium obcaecati, quae odit, ex cum
          nisi nam molestias assumenda quaerat ipsum facere. Id aperiam iure,
          consectetur eum illum voluptas ipsam saepe molestiae nulla voluptate
          facere. Atque amet sit ullam voluptate?
        </p>

        <div>
          <CSSTransition
            in={size.width > 10 && size.width < 300}
            timeout={300}
            classNames="size"
            unmountOnExit
          >
            <span>yaxis {size.width} </span>
          </CSSTransition>
        </div>
      </div>
    </div>
  );
}
