import "./index.scss";
import Button from "elements/Button";
import $ from "jquery";
import React, { Component } from "react";
import Emoji from "utils/Emoji";

export default class ExampleImageSlider extends Component {
  constructor() {
    super();
    this.scroll = this.scroll.bind(this);
  }

  scroll(direction) {
    let far = ($(".image-container").width() / 1.5) * direction;
    let pos = $(".image-container").scrollLeft() + far;
    $(".image-container").animate({ scrollLeft: pos }, 500);
  }

  render() {
    return (
      <div className="main">
        {/* <h1 className="title">Scrolling Image Slider</h1> */}
        <div className="wrapper">
          {/* <Button type="link" href="#" onClick={this.scroll.bind(null, -1)}>
            <svg
              width="18"
              height="31"
              viewBox="0 0 18 31"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M16.5 1.5L3.5 15.5L16.5 29.5"
                stroke="black"
                strokeWidth="2"
              />
            </svg>
          </Button> */}

          <div className="image-container">
            <div className="image">
              <Emoji symbol={0x1f352} className="mr-2" />
              Cheery
            </div>
            <div className="image">
              <Emoji symbol={0x1f347} className="mr-2" />
              Grapes
            </div>
            <div className="image">
              <Emoji symbol={0x1f348} className="mr-2" />
              Melon
            </div>
            <div className="image">
              <Emoji symbol={0x1f34d} className="mr-2" />
              Pineapple
            </div>
            <div className="image">
              <Emoji symbol={0x1f951} className="mr-2" />
              Avocado
            </div>
            <div className="image">
              <Emoji symbol={0x1f34c} className="mr-2" />
              Banana
            </div>
            <div className="image">
              <Emoji symbol={0x1f353} className="mr-2" />
              Strawberry
            </div>
            <div className="image">
              <Emoji symbol={0x1f34b} className="mr-2" />
              Lemon
            </div>
            <div className="image">
              <Emoji symbol={0x1f350} className="mr-2" />
              Pear
            </div>
            <div className="image">
              <Emoji symbol={0x1f965} className="mr-2" />
              Coconut
            </div>
          </div>
          {this.props.desk && (
            <Button type="link" href="#" onClick={this.scroll.bind(null, 1)}>
              <svg
                width="18"
                height="31"
                viewBox="0 0 18 31"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M1.5 29.5L14.5 15.5L1.5 1.5"
                  stroke="black"
                  strokeWidth="2"
                />
              </svg>
            </Button>
          )}
        </div>
      </div>
    );
  }
}
