import React from "react";
import Item from "../Item";
export default function SrResultDesktop(props) {
  return (
    <div
      style={{
        position: "fixed",
        top: 73,
        right: 0,
        left: 45,
        zIndex: 6,
      }}
    >
      <div className="container">
        <div
          className="row d-flex justify-content-end"
          style={{ paddingRight: 140 }}
        >
          {" "}
          <div className="p-3">
            {props.data.map((value, index) => {
              return <Item isDesktop key={`desk-${index}`} val={value} />;
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
