import React from "react";
import Fade from "react-reveal/Fade";
import Button from "elements/Button";

export default function Categories({ data, width }) {
  // Set 'width' variable from props
  const widths = width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 400;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  const breakpointXs2 = 359.5;
  const breakpointXs1 = 413.5;
  const breakpointXs = 469.5;
  const breakpointSm1 = 575.5;
  const breakpointSm = 767.5;
  const breakpointMd = 991.5;
  const breakpointLg = 1200.5;

  // Initialize array variable
  const width_responsive = [];
  // Responsive Logic
  widths < breakpointXs2 && width_responsive.push("xs2");
  widths > breakpointXs2 &&
    widths < breakpointXs1 &&
    width_responsive.push("xs1");
  widths > breakpointXs1 &&
    widths < breakpointXs &&
    width_responsive.push("xs");
  widths > breakpointXs &&
    widths < breakpointSm1 &&
    width_responsive.push("sm1");
  widths > breakpointSm1 &&
    widths < breakpointSm &&
    width_responsive.push("sm");
  widths > breakpointSm && widths < breakpointMd && width_responsive.push("md");
  widths > breakpointMd && widths < breakpointLg && width_responsive.push("lg");

  // Map Title Categories
  return data.map((category, index1) => {
    return (
      <section className="container" key={`category-${index1}`}>
        <Fade delay={300} duration={1000}>
          <h4 className="mb-3 font-weight-medium">{category.name}</h4>
          <div
            className={`container-grid ${width_responsive.join()}`}
            style={{ gridAutoRows: 150 }}
          >
            {/* When category item undefined */}
            {category.items.length === 0 ? (
              <div className="row">
                <div className="col-auto align-items-center">
                  There is no property at this category
                </div>
              </div>
            ) : (
              //  Map Categories Catalog
              category.items.map((item, index2) => {
                return (
                  <div
                    className="item column-3 row-1"
                    key={`category-${index1}-item-${index2}`}
                  >
                    <div className="card card-favorite">
                      <figure className="img-wrapper">
                        <img
                          src={process.env.REACT_APP_PATH + item.imageUrl}
                          alt={item.name}
                          className="img-cover"
                        ></img>
                      </figure>
                      <div className="meta-wrapper">
                        {/* isExternal Link to Details Page */}
                        <Button
                          className="stretched-link d-block text-white"
                          type="link"
                          href={
                            process.env.REACT_APP_PATH + `/details/${item._id}`
                          }
                        >
                          <h5 className="h4">{item.name}</h5>
                        </Button>

                        <span style={{ fontSize: 11 }}>
                          $<span style={{ marginLeft: 2 }}>{item.price}</span>
                        </span>
                        <p style={{ fontSize: 11 }}>{`Order now >`}</p>
                      </div>
                    </div>
                  </div>
                );
              })
            )}
          </div>
        </Fade>
      </section>
    );
  });
}
