import React from "react";
import LottieElem from "elements/Animate/index";
import animationData from "assets/anim/404.json";

export default function NotFound() {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  return <LottieElem is404 data={defaultOptions} width={400} height={400} />;
}
