import React from "react";
import "./index.scss";
import { Link } from "react-router-dom";

export default function ExampleAnim() {
  return (
    <div>
      <div className="transition">
        <div className="container">
          <h1>Animate Example</h1>
          <ul>
            <li>
              <Link to="/example-trans">Transition Group</Link>
            </li>
            <li>
              <Link to="/example-green">Greenshock</Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
