import React, { Component } from "react";

import SrResultDesktop from "elements/Search/State/mainres/SrResultDesktop";

import Header from "parts/header/Header";
import PageDetailTitle from "parts/PageDetailTitle";
import ImageCarousel from "parts/ImageCarousel";
import PageDetailDescription from "parts/PageDetailDescription";
import CollapseText from "parts/CollapseText";
import Categories from "parts/Categories";
import CommentRate from "parts/CommentRate";
import Footer from "parts/Footer";
import CartData from "json/Cart.json";

import ItemDetails from "json/ItemDetails.json";

import data from "json/MainSearch.json";

export default class DetailsPage extends Component {
  constructor(props) {
    super(props);

    this.handlersrdesk = this.handlersrdesk.bind(this);
    this.handlerdesk = this.handlerdesk.bind(this);
  }

  // Set state component
  state = {
    screenWidth: window.innerWidth,
    scrollYaxis: window.scrollY,
    searchInpDesk: "",
  };
  handlerdesk() {
    this.setState({
      searchInpDesk: undefined,
    });
  }

  handlersrdesk(e) {
    this.setState({
      searchInpDesk: e.target.value,
    });
  }
  // Handle Event DOM
  handleResize = () => {
    this.setState({ screenWidth: window.innerWidth });
  };
  handleScroll = () => {
    this.setState({ scrollY: window.scrollY });
  };

  // Every time render in the DOM for the first time 'mount'
  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    window.addEventListener("scroll", this.handleScroll);
  }
  // Detach from DOM 'unmount'
  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
    window.removeEventListener("scroll", this.handleScroll);
  }

  render() {
    // Destructure this state
    const { screenWidth, scrollYaxis } = this.state;

    // Set 'screen size' variable
    // const breakpointCustom = 400;
    // const breakpointNull = 0;
    // const breakpointXs3 = 280.5;
    // const breakpointXs2 = 359.5;
    // const breakpointXs = 469.5;
    // const breakpointSm = 767.5;
    // const breakpointMd = 991.5;
    const breakpointLg = 1200.5;

    const breadcrumbList = [
      { pageTitle: "Home", pageHref: "" },
      { pageTitle: "Details", pageHref: "" },
    ];

    const resultsdesk = !this.state.searchInpDesk
      ? ""
      : data.search.filter((val) =>
          val.title
            .toLowerCase()
            .includes(this.state.searchInpDesk.toLocaleLowerCase())
        );

    return (
      <>
        {/* Main Function Component */}
        <Header
          {...this.props}
          width={screenWidth}
          scrollY={scrollYaxis}
          notif={CartData}
          isDetailSr
          handlerdesk={this.handlerdesk}
          handlersrdesk={this.handlersrdesk}
        />

        {/* Search Result Dekstop */}
        {resultsdesk && screenWidth > breakpointLg ? (
          <SrResultDesktop isDesktop data={resultsdesk} />
        ) : null}

        {/* Main Function Component */}
        <PageDetailTitle breadcrumb={breadcrumbList} data={ItemDetails} />
        <section>
          <div className="container">
            <div
              className={`row ${
                screenWidth > 469.5 && screenWidth < 991.5
                  ? "justify-content-center"
                  : ""
              } `}
            >
              <div className="col-lg-auto col-sm-auto col-auto ">
                <ImageCarousel data={ItemDetails} width={screenWidth} />
              </div>
              <div className="col-lg-5 col-sm-10 col-12 ">
                <PageDetailDescription data={ItemDetails} width={screenWidth} />
              </div>
            </div>
          </div>
        </section>
        <div className="mb-5">
          <CollapseText data={ItemDetails.features} />
        </div>
        <Categories data={ItemDetails.categories} width={screenWidth} />
        <section>
          <div className="container">
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h4>Review</h4>
              <h6 style={{ display: "flex" }}>
                <a
                  href="/#"
                  style={{
                    textDecoration: "none",
                    color: "black",
                    backgroundColor: "#FFDD64",
                    borderRadius: "30px",
                    padding: "5px 10px",
                    fontSize: 12,
                    margin: "auto",
                    marginBottom: 15,
                  }}
                >
                  Other Review
                </a>
              </h6>
            </div>

            <CommentRate data={ItemDetails.commentRate} width={screenWidth} />
          </div>
        </section>
        <Footer />
      </>
    );
  }
}
