import React from "react";

export default function Basket(props) {
  const { cartItems, onAdd, onRemove } = props;
  const itemsPrice = cartItems.reduce((a, c) => a + c.price * c.qty, 0);
  const taxPrice = itemsPrice * 0.14;
  const shippingPrice = itemsPrice > 2000 ? 0 : 50;
  const totalPrice = itemsPrice + taxPrice + shippingPrice;

  return (
    <div className="block cols-1">
      <h2>Basket</h2>
      <div>{cartItems.length === 0 && <div>Cart is empty</div>}</div>
      {cartItems.map((item) => {
        return (
          <div key={item.id} className="rows">
            <div className="cols-2">{item.name}</div>
            <div className="cols-2">
              <button onClick={() => onAdd(item)} className="add">
                +
              </button>
              <button onClick={() => onRemove(item)} className="remove">
                -
              </button>
            </div>
            <div className="cols-2 text-right">
              {item.qty} x ${item.price.toFixed(2)}
            </div>
          </div>
        );
      })}
      {cartItems.length !== 0 && (
        <>
          <hr></hr>
          <div className="rows">
            <div className="cols-2">Items Price</div>
            <div className="cols-1 text-right">${itemsPrice.toFixed(2)}</div>
          </div>
          <div className="rows">
            <div className="cols-2">Tax Price</div>
            <div className="cols-1 text-right">${taxPrice.toFixed(2)}</div>
          </div>
          <div className="rows">
            <div className="cols-2">Shipping Price</div>
            <div className="cols-1 text-right">${shippingPrice.toFixed(2)}</div>
          </div>
          <div className="rows">
            <div className="cols-2">
              <strong>Total Price</strong>
            </div>
            <div className="cols-1 text-right">${totalPrice.toFixed(2)}</div>
          </div>
    <hr />
    <div className="rows">
      <button onClick={()=>alert('Implement checkout')}>Checkout</button>
    </div>
        </>
      )}
    </div>

  );
}
