import React from "react";
import Star from "elements/Star";
import Fade from "react-reveal/Fade";

export default function CommentRate({ data, width }) {
  // Set 'width' variable from props
  const widths = width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 400;
  // const breakpointNull = 0;
  //const breakpointXs3 = 280.5;
  const breakpointXs2 = 359.5;
  // const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  // const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;

  // Map Comment max slice 4 data
  return data.slice(0, 3).map((item, index) => {
    return (
      <div key={`comment-${index}`}>
        <Fade delay={300} duration={1000}>
          <div
            className="card "
            style={{
              borderRadius: 4,
              backgroundColor: "white",
              marginTop: -5,
              padding: "20px 20px 20px 20px",
              boxShadow:
                "0 5px 10px 0 rgba(0, 0, 0, 0.05), 0 10px 10px 0 rgba(0, 0, 0, 0.05)",
            }}
          >
            <div className="row">
              <div className="col-auto">
                {/* Element Star */}
                <Star value={item.rate} width={35} height={35} spacing={4} />
              </div>
              {/* Render or Hide Long time value  */}
              {/* [less than < 359px] */}
              {widths < breakpointXs2 && (
                <div className="col my-auto ">
                  <span>{item.longTime}</span>
                </div>
              )}
            </div>
            <div className="ml-2">
              <h6>{item.name}</h6>
              <span>{item.comment}</span>
            </div>
          </div>
        </Fade>
      </div>
    );
  });
}
