import React from "react";
import Button from "elements/Button";

export default function Product({ product, onAdd }) {
  return (
    <div>
      <div style={{ maxWidth: 400, padding: 10 }}>
        <img
          src={product.image}
          alt=""
          style={{ width: "100%", borderRadius: 5 }}
        />
      </div>
      <div style={{ padding: 10 }}>
        <h6>{product.name}</h6>
        <h6>$ {product.price}</h6>
        <Button isPrimary isSmall onClick={() => onAdd(product)}>
          Add
        </Button>
      </div>
    </div>
  );
}
