import React from "react";
import Fade from "react-reveal/Fade";
import ImageHero from "assets/images/image-hero.png";
import Star from "assets/icon/star.svg";
import Button from "elements/Button";
import Carousels from "parts/imageslider/ExampleImageSlider";

export default function Hero(props) {
  // set Ref and event ScrollTo
  function showMostFavorite() {
    window.scrollTo({
      top: props.refMostFavorite.current.offsetTop - 100,
      behavior: "smooth",
    });
  }

  // Set 'width' variable from props
  const width = props.width;

  // Set 'screen size' variable from props
  // const breakpointCustom = 400;
  // const breakpointNull = 0;
  // const breakpointXs3 = 280.5;
  // const breakpointXs2 = 359.5;
  // const breakpointXs = 469.5;
  // const breakpointSm = 767.5;
  const breakpointMd = 991.5;
  // const breakpointLg = 1200.5;
  const isDesk = {
    desk: true,
  };
  return (
    <Fade>
      {/* When [props.show=false] add className=['mt'] not className=['top-margin'] */}
      <section
        className={`container px-4 ${!props.show ? "top-margin" : "mt-4"} `}
      >
        <Carousels {...(width > breakpointMd && isDesk)}></Carousels>
        <div className="row mt-4">
          <div className="col-lg-6" style={{ width: 523 }}>
            <h1 className="font-weight-bold" style={{ marginBottom: 12 }}>
              Do You Want,
              <br />
              Delicious Cake Today
            </h1>
            <p className="mb-4 text-gray-700 ">
              we provide some beautiful cakes <br /> and suitable for you to
              make as a event cake or also a party cake
            </p>
            <Button
              className="btn px-5 py-2"
              isPrimary
              hasShadow
              style={{ borderRadius: 25 }}
              // Handle onClick scroll to mostFavorite Component
              onClick={showMostFavorite}
            >
              Show me
            </Button>

            <div className="row" style={{ marginTop: 40 }}>
              <div className="col-auto mb-3">
                {/* Map Avatar image Review */}
                {props.data.ImageUserReview.map((item, index) => {
                  return (
                    <img
                      key={`avatar-${index}`}
                      src={process.env.REACT_APP_PATH + item.imageurl}
                      alt={item.name}
                      style={{
                        width: 56,
                        borderRadius: 30,
                        border: "3px solid white",
                        // outline: "solid",
                        // outlineColor: "white",
                        // When index more than 0 set value property [marginleft -15]
                        marginLeft: index > 0 ? -15 : "",
                      }}
                    />
                  );
                })}
              </div>

              <div className="col-lg-7 col-sm-8 col-9">
                <h6>{props.data.title}</h6>
                <p>
                  <img src={Star} alt="star" /> {props.data.value}
                  {props.data.review}
                </p>
              </div>
            </div>
          </div>

          <div className="col-lg-6">
            {/* When width [more than > 991px] Render Image Hero  */}
            {width > breakpointMd && (
              <div className="img-hero">
                <img src={ImageHero} className="img-fluid" alt="" />
              </div>
            )}
          </div>
        </div>
      </section>
    </Fade>
  );
}
