import "assets/scss/bootstrap.scss";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import LandingPage from "pages/LandingPage";
import DetailsPage from "pages/DetailsPage";
import Service from "pages/Service";
import ExampleRCC from "pages/Example/ExampleRCC";
import ExampleRFC from "pages/Example/ExampleRFC";
import ScrollMemory from "react-router-scroll-memory";
import ExampleHeader from "pages/Example/ExampleHeader";
import ExampleHeaderRFC from "pages/Example/ExampleHeaderRFC";
import CartRCC from "pages/Cart/NotFinished/CartRCC";
import Search from "pages/Search";
import ImageShow from "parts/ImageShow";
import NotFound from "pages/NotFound";

import StateTest from "pages/Example/StateTest";
import SearchSamples from "pages/Example/SearchSamples";
import ExampleCart from "pages/Example/component/ExampleCart";
import ExampleRedux from "pages/Example/redux/ExampleRedux";
import ExampleAnim from "pages/Example/animate/ExampleAnim";
import Trans from "pages/Example/animate/transitiongroup/Trans";
import HiddenMenu from "pages/Hidden/HiddenMenu";
import ExampleTransition1 from "pages/Example/animate/transitiongroup/transitionOne/ExampleTransition";
import ExampleTransition2 from "pages/Example/animate/transitiongroup/transitionTwo/ExampleTransition2";
import ExampleGreen from "pages/Example/animate/greenshock/ExampleGreen";
import Cart from "pages/Cart/Cart";
import ImageSlider from "pages/Example/imageslider/ExampleImageSlider";

function App() {
  return (
    <div className="App">
      <Router>
        <ScrollMemory />
        <Switch>
          {/* Main */}

          <Route
            exact
            path={process.env.REACT_APP_PATH}
            component={LandingPage}
          />
          <Route
            exact
            path={process.env.REACT_APP_PATH + "/details/:id"}
            component={DetailsPage}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/service"}
            component={Service}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/cartRCC"}
            component={CartRCC}
          />
          <Route path={process.env.REACT_APP_PATH + "/cart"} component={Cart} />
          <Route
            path={process.env.REACT_APP_PATH + "/search"}
            component={Search}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/image-show"}
            component={ImageShow}
          />

          {/* Example */}
          <Route
            path={process.env.REACT_APP_PATH + "/hidden"}
            component={HiddenMenu}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-rcc"}
            component={ExampleRCC}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-rfc"}
            component={ExampleRFC}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-header"}
            component={ExampleHeader}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-header-rfc"}
            component={ExampleHeaderRFC}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-cart"}
            component={ExampleCart}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/search-ex"}
            component={SearchSamples}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/state"}
            component={StateTest}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-redux"}
            component={ExampleRedux}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/image-slider"}
            component={ImageSlider}
          />

          {/* Anim */}
          <Route
            path={process.env.REACT_APP_PATH + "/example-anim"}
            component={ExampleAnim}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-trans"}
            component={Trans}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-trans-1"}
            component={ExampleTransition1}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-trans-2"}
            component={ExampleTransition2}
          />
          <Route
            path={process.env.REACT_APP_PATH + "/example-green"}
            component={ExampleGreen}
          />

          {/* Not Found */}
          <Route path="*" component={NotFound} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
