import Button from "elements/Button";
import React, { useState } from "react";
import ReactHtmlParser from "react-html-parser";
import Fade from "react-reveal/Fade";
import ArrowDown from "assets/icon/arrow-down.svg";

export default function CollapseText({ data }) {
  // Set state Collapse
  const [isCollapse1, setCollapse1] = useState(false);
  const [isCollapse2, setCollapse2] = useState(false);
  const [isCollapse3, setCollapse3] = useState(false);

  // Map Collapse Text
  return data.map((item, index) => {
    return (
      <div key={`collapse-text-${index}`} className="container">
        <Fade bottom delay={300} duration={1000}>
          <div className="accordion">
            <div className="card" style={{ borderRadius: 0 }}>
              <div className="card-header">
                <h5 className="mb-0">
                  <Button
                    type="link"
                    href="#"
                    className="text-decoration-none h6 font-weight-regular text-dark"
                    //  handle onClick and if index position === 1||2||3 set isCollapse ['false']
                    onClick={
                      index === 0
                        ? () => setCollapse1(!isCollapse1)
                        : "" || index === 1
                        ? () => setCollapse2(!isCollapse2)
                        : "" || index === 2
                        ? () => setCollapse3(!isCollapse3)
                        : ""
                    }
                  >
                    <div className="row">
                      <div className="col-lg-11 col-md-11 col-sm-10 col-10">
                        {item.title}
                      </div>
                      <div className="col-lg-auto col-md-auto col-sm-auto col-2">
                        <img src={ArrowDown} alt="" width="15px" />
                      </div>
                    </div>
                  </Button>
                </h5>
              </div>

              <div
                id="collapseOne"
                // When index position === 1||2||3 and state Collapse ['true']
                // Set className=['show']
                className={`collapse ${
                  index === 0 && isCollapse1
                    ? "show"
                    : "" || (index === 1 && isCollapse2)
                    ? "show"
                    : "" || (index === 2 && isCollapse3)
                    ? "show"
                    : ""
                }`}
                aria-labelledby="headingOne"
                data-parent="#accordionExample"
              >
                {/* Parser HTML String to React Element */}
                <div className="card-body">{ReactHtmlParser(item.explain)}</div>
              </div>
            </div>
          </div>
        </Fade>
      </div>
    );
  });
}
